@import url('https://fonts.googleapis.com/css2?family=Climate+Crisis&family=IBM+Plex+Mono:ital,wght@0,700;1,700&family=Roboto:ital,wght@0,100;1,900&family=Schibsted+Grotesk&display=swap');
/* *{
    user-select: none;
    pointer-events: none;
} */
body::-webkit-scrollbar {
    display: none;
}
.personal-info{
    margin: 0px;
    padding: 5% 10%;
    background-color: #FF9F1C;
    height: 800px;
    overflow: hidden;
}
.personal-details{
    /* border: 1px solid black; */
    display: flex;
    width: 100%;
}
.detail{
    /* border: 1px solid black; */
    flex: 1;
    font-size: 16px;
    font-family: 'Schibsted Grotesk', sans-serif;
}
.name{
    font-size: 20.5vw;
    text-align: center;
    z-index: 2;
    position: absolute;
    font-family: 'Climate Crisis', cursive;
    /* border: 2px solid greenyellow; */
}
.canvas-container{
    margin-top: 300px;
    width: 80vw;
    height: 100vh;
    position: relative;
    z-index: 2;
    filter: drop-shadow( 0px 0px 16px #2EC4B6)
}
.star-bg{
    width: 80vw;
    position: absolute;
    top: 10em;
}
.info-container{
    display: flex;
    margin: 0px;
    padding: 5% 10%;
    background-color: #011627;
    /* width: 100%; */
    justify-content: start;
    position: relative;
    flex-direction: column;
}
.myintro{
    color: #2EC4B6;
    width: 50%;
    font-family: 'Climate Crisis', cursive;
    font-size: 55px;
    /* border: 1px solid white; */
}
.hello{
    width: 100px;
    color: white;
    font-family: 'Schibsted Grotesk', sans-serif;
    font-size: 20px;
    /* border: 1px solid white; */
}
.education-container{
    display: flex;
    margin: 0px;
    padding: 5% 10%;
    background-color: #011627;;
    /* width: 100%; */
    justify-content: start;
    position: relative;
    flex-direction: column;
}
.edu-details-title{
    color: white;
    font-family: 'Climate Crisis', cursive;
    font-weight: 400;
}
.edu-details-container{
    display: flex;
    color: white;
    font-family: 'Schibsted Grotesk', sans-serif;
}
.edu-item,.edu-year{
    flex: 1;
    font-size: 20px;
    margin-bottom: 0px;
}
.skill-container{
    display: flex;
    margin: 0px;
    padding: 5% 10%;
   background-color: #011627;
    object-fit: fill;
    /* width: 100%; */
    justify-content: start;
    position: relative;
    flex-direction: column;
}
.skill-title{
    color: white;
    font-family: 'Climate Crisis', cursive;
    font-size: 60px;
    font-weight: 400;
}
.skills-subcontainer{
    display: flex;
    color: white;
    font-family: 'Schibsted Grotesk', sans-serif;
}
.skill-set{
    display: flex;
    /* border: 1px solid white; */
    width: 100%;
    align-items: center;
    flex-wrap: wrap;
}
.skill-set img{
    width: 20px;
    margin-right: 5px;
    margin-left: 5px;
}
.globe{
    width: 80vw;
    height: 800px;
    position: absolute;
    /* background-color: rgba(255, 255, 255, 0.32); */
    z-index: 5;
}
.project-container{
    display: flex;
    margin: 0px;
    padding: 5% 10%;
    width: 80vw;
    justify-content: start;
    position: relative;
    flex-direction: column;
    background-color: #011627;
}
.project-section-title{
    color: white;
    font-family: 'Climate Crisis', cursive;
    font-size: 60px;
    font-weight: 400;
}
.project-item{
    flex-wrap: wrap;
    display: flex;
    flex-direction: column;
    justify-self: center;
    font-family: roboto;
    padding: 20px;
    background: rgba(255, 255, 255, 0.2);
    box-shadow: 0 0px 62px 10px rgba(46,196,182,0.37);
    backdrop-filter: blur( 20px ); 
    filter: saturate(2);
     -webkit-backdrop-filter: blur( 20px );
    border: 2px solid #2EC4B6;
    border-radius: 10px;
    text-align: center;
    transition: all 1s;
    color: aliceblue;
    flex: 1;
    align-items: center;
}
.project-display{
    width: 100%;
    display: flex;
    /* flex-direction: column; */
    justify-content: space-between;
    gap: 20px;
    /* border: 1px solid #FF9F1C; */
}
.p-item-name{
    display: flex;
    justify-content: center;
    flex-wrap: wrap;
    /* color: #2EC4B6; */
    font-family: Arial, Helvetica, sans-serif;
    font-style: normal;
    margin-top: 5px;
    margin-bottom: 5px;
}
.project-item > img{
    width: 10%;
    background-color: #FF9F1C;
    padding: 5px;
    border-radius: 50%;
}
.p-item-content{
    width: 100%;
    /* border: 1px solid white; */
    font-size: 15px;
    font-family: 'Roboto', sans-serif;
    font-weight: 100;
    /* text-align: justify; */
}
.social-container{
    display: flex;
    margin: 0px;
    padding: 2% 10%;
    width: 80vw;
    color: white;
    background-color: #011627;
    flex-wrap: wrap;
}
.social-element{
    display: flex;
    align-items: center;
    flex: 1;
    justify-content: center;
    filter: drop-shadow(0px 0px 10px rgba(255,255,255,0.5));
    /* border: 1px solid white; */
}
.social-element  img {
    width: 30px;
    margin: 0;
}
.social-element  p {
    margin: 0 5px;
    font-size: 15px;
    color : white;
}
hr{
    color: white;
    margin: 0px;
    align-self: center;
}
@media  screen and (max-width:720px){
    .canvas-container{
        margin-top: 100px;
    }
    .personal-info{
        height: 600px;
    }
    .star-bg{
        opacity: 0;
    }
    .project-display{
        flex-direction: column;
    }
}
@media screen and (max-width:600px) {
    .canvas-container{
        margin-top: 0px;
    }
    .star-bg{
        opacity: 0;
    }
    .myintro{
        width: 100%;
        font-size: 30px;
    }
    .skill-title{
        font-size: 40px;
    }
    .project-section-title{
        font-size: 40px;
    }
    .personal-info{
        height: 600px;
    }
    .social-container{
        padding: 5% 10%;
    }
}